import { EnvironmentProviders, makeEnvironmentProviders } from '@angular/core';

import { NgxAvatarConfig } from './ngx-avatar-config';
import { NGX_AVATAR_CONFIG } from './ngx-avatar-config.token';

export function provideEnvironmentNgxAvatar(avatarConfig?: NgxAvatarConfig): EnvironmentProviders {
  return makeEnvironmentProviders([
    {
      provide: NGX_AVATAR_CONFIG,
      useValue: avatarConfig ? avatarConfig : {},
    },
  ]);
}
